<template>
  
</template>

<script>
import WebFontLoader from 'webfontloader'

export default {
  name: 'FontLoader',
  created () {
    WebFontLoader.load({
      active: this.setFontLoaded,
      typekit: { id: 'jrv5gxy' },
      classes: false
    })
  },
  methods: {
    setFontLoaded() {
      this.$emit('fontLoaded');
    },
  },
}
</script>

<style>
  body { font-family: 'Europa', sans-serif; }
</style>
